/* Icon Font */
@import url(http://weloveiconfonts.com/api/?family=typicons);
@import url(http://weloveiconfonts.com/api/?family=fontawesome);
@import url(https://fonts.googleapis.com/css?family=Allura|Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Quicksand);
[class*="typicons-"]:before {
  font-family: 'Typicons', sans-serif;
  position: absolute;
  left: 27%;
  top: 40%;
  font-size: 10em;
  color: #46C8E4;
  z-index: 100;
}

[class*="fontawesome-"]:before {
  font-family: 'FontAwesome', sans-serif;
  color: white;
  font-size: 0.9em;
  margin-right: 10px;
}

.text {
  font-family: 'Open Sans';
  margin-left: 20px;
}

.container {
  width: 100%;
  padding: 3em 0;
  text-align: center;
  position: relative;
}

.photo {
  border: 20px solid #46C8E4;
  width: 333px;
  position: relative;
  margin: 0 auto;
  height: 417px;
}

.photo-info {
  position: absolute;
  background-color: #fff;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0;
  transition: all 550ms ease-out 0s;
  z-index: 1;
}

.photo-info h3 {
  font-family: Allura;
  letter-spacing: 4px;
  font-size: 2em;
}

.photo-info p {
  font-size: 1em;
  font-variant: small-caps;
  letter-spacing: 0px;
  font-family: Open Sans;
}

.hover {
  position: absolute;
  display: none;
  background-color: #46C8E4;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  height: auto;
  text-align: left;
  padding: 20px;
  z-index: 1000;
  border: 3px solid #fff;
}

.hover h2 {
  font-size: 2em;
  text-transform: uppercase;
  font-family: Quicksand;
  font-weight: bold;
  color: #fff;
}

.hover span {
  font-size: 1.4em;
  color: #fff;
  letter-spacing: 2px;
  font-family: Quicksand;
}

nav {
  position: absolute;
  bottom: 20px;
  left: 0px;
  font-size: 0.7em;
}
